import React, { useState, useRef, useEffect } from "react";
import ReactCrop, { Crop, PixelCrop } from "react-image-crop";
import { Streamlit, withStreamlitConnection } from "streamlit-component-lib";
import { canvasPreview } from "./CanvasPreview";
import { useDebounceEffect } from "./utils";

import "react-image-crop/dist/ReactCrop.css";

const CropTool = ({args}: any) => {
  const previewCanvasRef = useRef<HTMLCanvasElement>(null);
  const imgRef = useRef<HTMLImageElement>(null);
  const [crop, setCrop] = useState<Crop>();
  const [completedCrop, setCompletedCrop] = useState<PixelCrop>();

  useEffect(() => Streamlit.setFrameHeight());
  useEffect(() => {
    if (args.initialCrop !== null) {
      const newCrop = {
        unit: "px",
        ...args.initialCrop
      };
      setCrop(newCrop);
      Streamlit.setComponentValue(newCrop);
    }
  // eslint-disable-next-line
  }, [JSON.stringify(args.initialCrop)]);

  useDebounceEffect(
    async () => {
      if (
        completedCrop?.width &&
        completedCrop?.height &&
        imgRef.current &&
        previewCanvasRef.current
      ) {
        Streamlit.setComponentValue(crop);
        canvasPreview(imgRef.current, previewCanvasRef.current, completedCrop);
      } else if (completedCrop !== undefined && completedCrop.width === 0 && completedCrop.height === 0) {
        // Signal back that the user cancelled selection
        Streamlit.setComponentValue(null);
      }
    },
    50,
    [completedCrop]
  );

  return (
    <div className="App">
      <div className="crop-container">
        <div className="crop-controls">
          <div style={{display: args.previewResult ? "block" : "none"}}>
            {Boolean(completedCrop) && (
              <>
                <h2>Preview:</h2>
                <canvas
                  className="gradient-border"
                  ref={previewCanvasRef}
                  style={{
                    objectFit: "contain",
                    width: completedCrop?.width,
                    height: "auto",
                  }}
                />
              </>
            )}
          </div>
        </div>
        <ReactCrop
          className="gradient-border-canvas"
          crop={crop}
          onChange={(crop, _) => setCrop(crop)}
          onComplete={(c) => setCompletedCrop(c)}
          {...args.kwargs}
        >
          <img ref={imgRef} alt="Crop me" src={args.image} onLoad={() => Streamlit.setFrameHeight()} />
        </ReactCrop>
      </div>
    </div>
  );
};

export default withStreamlitConnection(CropTool);
